/* width */
::-webkit-scrollbar {
    width: 10px;
}
  
  /* Track */
::-webkit-scrollbar-track {
    background: #282c34;
}
  
  /* Handle */
::-webkit-scrollbar-thumb {
    background-color: #39445a;
    background: #888;
    border-radius: 0.25em;
}
  
  /* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

