.Message-body {
    -webkit-animation-name: messmovertopbottom;
            animation-name: messmovertopbottom;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    background-color: #353a46;
    border-style: solid;
    border-color: #242730;
    position: absolute;
    border-radius: 7px;
    width: auto;
    height: auto;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* margin-left: auto; 
    margin-right: auto;
    margin: auto;  */
}

